// new Function è§£æå­—ç¬¦ä¸²
const template2 = (str, data) => {
  const fn = new Function("users",

    "var p = []; p.push('" +

    str
      .replace(/[\r\t\n]/g, "")
      .replace(/<%=(.*?)%>/g, "');p.push($1);p.push('")
      .replace(/<%/g, "');")
      .replace(/%>/g,"p.push('")
    + "');return p.join('');");
  return tagMin(fn(data));
};

// æ•°æ®æ”¹å˜ new Function åªè°ƒç”¨ä¸€æ¬¡
const template3 = str => {
  const _str = tagMin(str);
  const fn = new Function("users",

    "var p = []; p.push('" +

    _str
      .replace(/[\r\t\n]/g, "")
      .replace(/<%=(.*?)%>/g, "');p.push($1);p.push('")
      .replace(/<%/g, "');")
      .replace(/%>/g,"p.push('")
    + "');return p.join('');");
  return data => fn.call(this, data)
};

