const escapeChar = function(match) {
  const escapes = {
    "'": "'",
    '\\': '\\',
    '\r': 'r',
    '\n': 'n',
    '\u2028': 'u2028', // è¡Œåˆ†éš”ç¬¦
    '\u2029': 'u2029' // æ®µè½åˆ†éš”ç¬¦
  };
  return '\\' + escapes[match];
};

